@import '../../styles/_variables.scss';

.projects-section {
  padding: 4rem 2rem;
  background-color: $background-color;
  color: $text-color;
  text-align: center;

  h2 {
    color: $primary-color;
    text-transform: uppercase;
    margin-bottom: 2rem;
    font-size: 2.5rem;

    @media (max-width: 768px) {
      font-size: 2rem; /* Ajustement du titre sur mobile */
    }
  }

  .projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Adaptatif pour différents écrans */
    gap: 2rem; /* Espacement entre les cartes */
    max-width: 1200px; /* Largeur maximale de la grille */
    margin: 0 auto; /* Centrer la grille */

    @media (max-width: 768px) {
      gap: 1.5rem; /* Réduction de l'espacement sur mobile */
    }
  }
  
  .project-card {
    background: linear-gradient(135deg, $card-bg, lighten($card-bg, 10%)); // Ajout d'un léger gradient
    padding: 1.5rem;
    border-radius: 15px;
    max-width: 400px; /* Largeur maximale de chaque carte */
    width: 100%;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.4s ease, box-shadow 0.4s ease;

    &:hover {
      transform: translateY(-15px) scale(1.05);
      box-shadow: 0 15px 25px rgba(0, 0, 0, 0.2);
    }

    .project-image {
      width: 100%;
      height: 200px;
      object-fit: cover;
      object-position: top;
      border-radius: 10px;
      margin-bottom: 1.5rem;
    }

    .project-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 1rem;

      .project-logo {
        width: auto; // Ajustement de la taille pour une meilleure harmonie
        height: 33px;
        margin-bottom: 1rem;
      }

      p {
        font-size: 1rem;
        color: $secondary-color;
        margin-bottom: 1.5rem;
        text-align: center;

        @media (max-width: 768px) {
          font-size: 0.9rem; /* Ajustement de la taille de la police sur mobile */
        }
      }

      .project-link,
      .project-repo {
        text-decoration: none;
        width: 70%;
        color: #fff;
        background-color: $primary-color;
        padding: 0.8rem 1.2rem;
        border-radius: 8px;
        font-weight: bold;
        margin-top: 1rem;
        transition: background-color 0.3s ease, transform 0.3s ease;

        &:hover {
          background-color: $primary-hover;
          transform: scale(1.05);
        }

        @media (max-width: 768px) {
          width: 100%; /* Adapte les liens pour qu'ils prennent toute la largeur sur mobile */
        }
      }

      .project-repo {
        margin-top: 0.5rem;
      }
    }
  }
}
